@import url(https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald:300,400,700&display=swap);
.hero {
  height: 100vh;
  margin: 0;
  padding: 0; }

#about-me {
  height: 100vh; }
  #about-me img {
    border-radius: 50%;
    height: 200px;
    margin-right: 16px; }
  #about-me h1 {
    margin-bottom: 16px;
    font-size: 2rem; }

form input, form textarea {
  width: 100%; }

nav ul {
  list-style: none; }

nav {
  background-color: aqua;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50px; }
  nav a {
    text-decoration: none;
    color: #000;
    color: initial; }

.font-roboto {
  font-family: Roboto; }

.font-montserrat {
  font-family: Montserrat, SansSerif; }

.font-Oswald {
  font-family: Oswald; }

.greenery {
  background-color: #86AF49; }

.fruit-salad {
  background-color: #54A557; }

.green-haze {
  background-color: #019967; }

.observatory {
  background-color: #008C75; }

.teal {
  background-color: #007D80; }

.blue-lagoon {
  background-color: #006E84; }

.font-greenery {
  color: #86AF49; }

.font-fruit-salad {
  color: #54A557; }

.font-green-haze {
  color: #019967; }

.font-observatory {
  color: #008C75; }

.font-teal {
  color: #007D80; }

.font-blue-lagoon {
  color: #006E84; }

* {
  font-family: 'Montserrat', SansSerif; }

*, *:after, *:before {
  box-sizing: border-box; }

body {
  padding-top: 50px;
  margin: 0; }

section {
  margin: 16px 0;
  padding: 0 20vw;
  min-height: 100vh; }
  section h2 {
    padding-top: 16px;
    margin-bottom: 0; }
  section > div {
    min-height: 30vh;
    margin-top: 32px; }

p {
  line-height: 1.4rem;
  text-indent: 2rem;
  text-align: justify; }

a, a:hover {
  text-decoration: none;
  color: #006E84; }
  a:hover {
    color: #008C75; }

.bold {
  font-weight: bold; }

.italic {
  font-style: italic; }

.underline {
  text-decoration: underline; }

.description {
  font-size: .8em;
  text-indent: 0;
  padding: 3px;
  margin: 0; }

.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.flex-column {
  flex-direction: column; }

.flex-start {
  justify-content: start; }

.center, .center-all {
  justify-content: center; }

.nowrap {
  flex-wrap: nowrap; }

.center-all {
  align-content: center;
  align-items: center; }

.flex-space-evenly {
  justify-content: space-evenly; }

#color-mode {
  position: fixed;
  bottom: 8px;
  right: 16px; }
  #color-mode img {
    height: 40px;
    cursor: pointer; }

